body {
  font-family: "Roboto Condensed", "Arial", sans-serif;
}

.clear-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 32px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.aboutus {
  padding: 25px 25px 80px 25px;
}
@media (min-width: 768px) {
  .aboutus {
    display: flex;
    gap: 30px;
    padding: 25px 49px 80px 49px;
  }
}
@media (min-width: 1920px) {
  .aboutus {
    gap: 100px;
    padding: 75px 350px 65px 350px;
  }
}

.aboutus__illustration {
  display: none;
}
@media (min-width: 768px) {
  .aboutus__illustration {
    display: inline-block;
  }
}

.aboutus__title {
  margin-top: 0;
  margin-bottom: 15px;
  font-weight: 400;
  font-size: 28px;
  line-height: 40px;
  color: #041827;
}
@media (min-width: 768px) {
  .aboutus__title {
    font-size: 36px;
    line-height: 51px;
  }
}

.aboutus__text {
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 14px;
  line-height: 19px;
  color: #041827;
  margin-top: 0;
  margin-bottom: 20px;
}
@media (min-width: 768px) {
  .aboutus__text {
    font-size: 16px;
    line-height: 22px;
  }
}
.aboutus__text--hidden {
  display: none;
}
@media (min-width: 768px) {
  .aboutus__text--hidden {
    display: inline-block;
  }
}

.aboutus__button-more-wrapper {
  display: flex;
  justify-content: flex-start;
}

.aboutus__button-more {
  display: block;
  cursor: pointer;
  text-transform: uppercase;
  border: 1px solid #00B2FF;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  color: #00B2FF;
  padding: 8px 14px 12px 14px;
  width: 130px;
  box-sizing: border-box;
  border-radius: 3px;
  margin-top: 10px;
}
@media (min-width: 768px) {
  .aboutus__button-more {
    width: 180px;
    padding: 14px 12px;
    margin-top: 20px;
  }
}
.aboutus__button-more:hover {
  background-color: #00B2FF;
  color: #f2f5f6;
}

.advantages {
  padding: 50px 25px 25px 25px;
}
@media (min-width: 768px) {
  .advantages {
    padding: 80px 50px 55px 50px;
  }
}
@media (min-width: 1920px) {
  .advantages {
    padding: 120px 350px 55px 350px;
  }
}

.advantages__list {
  display: grid;
  grid-template-columns: 1fr;
  gap: 30px;
}
@media (min-width: 768px) {
  .advantages__list {
    grid-template-columns: 1fr 1fr;
  }
}

.advantages__item {
  display: flex;
  flex-direction: column;
  padding: 30px 25px;
  min-height: 340px;
  border: 1px solid #ebebeb;
  border-left: 5px solid #00B2FF;
  color: #00B2FF;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .advantages__item {
    min-height: 360px;
    padding: 40px 45px;
  }
}
@media (min-width: 1920px) {
  .advantages__item {
    min-height: 300px;
  }
}
.advantages__item:hover {
  border-color: #00B2FF;
  background-color: #00B2FF;
  color: #f2f5f6;
}
.advantages__item:hover > .advantages__title, .advantages__item:hover > .advantages__text {
  color: #f2f5f6;
}

.advantages__icon {
  width: 30px;
  height: 30px;
  margin-bottom: 30px;
}
@media (min-width: 768px) {
  .advantages__icon {
    width: 35px;
    height: 35px;
  }
}

.advantages__title {
  margin-top: 0;
  margin-bottom: 15px;
  color: #00B2FF;
  font-size: 20px;
  line-height: 34px;
  font-weight: 400;
}
@media (min-width: 768px) {
  .advantages__title {
    font-size: 24px;
  }
}

.advantages__text {
  margin: 0;
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 14px;
  line-height: 19px;
  color: #041827;
}
@media (min-width: 768px) {
  .advantages__text {
    font-size: 16px;
    line-height: 22px;
  }
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.contacts {
  display: flex;
  align-items: center;
  gap: 30px;
}
@media (min-width: 1920px) {
  .contacts {
    gap: 110px;
  }
}

.contacts__link {
  font-family: "PT Mono", "Arial", sans-serif;
  cursor: pointer;
  color: #f2f5f6;
  display: flex;
  align-items: center;
  gap: 15px;
  font-size: 16px;
  line-height: 18px;
  padding: 10px;
}

.contacts__link-text {
  display: none;
}
@media (min-width: 1920px) {
  .contacts__link-text {
    display: block;
  }
}

.footer {
  background-color: #041827;
}

.footer__wrapper {
  max-width: 1920px;
  box-sizing: border-box;
  margin: 0 auto;
  padding: 35px 25px 55px 25px;
}
@media (min-width: 768px) {
  .footer__wrapper {
    padding: 50px 50px 65px 50px;
  }
}
@media (min-width: 1920px) {
  .footer__wrapper {
    display: flex;
    justify-content: space-between;
    padding: 50px 350px 55px 350px;
  }
}

.footer__social {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 25px;
  margin-bottom: 30px;
}
@media (min-width: 768px) {
  .footer__social {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 35px;
    gap: 0;
  }
}
@media (min-width: 1920px) {
  .footer__social {
    display: block;
    margin-bottom: 0;
  }
}

.footer__social-list {
  display: flex;
  gap: 25px;
}
@media (min-width: 1920px) {
  .footer__social-list {
    margin-top: 35px;
    margin-left: 15px;
  }
}

.footer__chapters-title,
.footer__contacts-title {
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
  color: #ffffff;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  margin-top: 0;
  margin-bottom: 20px;
}

.footer__menu-wrapper {
  display: flex;
  flex-direction: column;
  gap: 40px;
}
@media (min-width: 768px) {
  .footer__menu-wrapper {
    flex-direction: row;
    gap: 110px;
  }
}
@media (min-width: 1920px) {
  .footer__menu-wrapper {
    gap: 280px;
  }
}

.footer__chapters-list {
  font-family: "PT Mono", "Arial", sans-serif;
  color: rgba(255, 255, 255, 0.7);
  font-size: 16px;
  line-height: 22px;
  display: flex;
  flex-direction: column;
  row-gap: 10px;
}
@media (min-width: 1920px) {
  .footer__chapters-list {
    max-height: 140px;
    flex-wrap: wrap;
    column-gap: 30px;
  }
}

@media (min-width: 1920px) {
  .footer__chapters-list-item {
    max-width: 220px;
  }
}

.footer__contacts-list {
  font-family: "PT Mono", "Arial", sans-serif;
  color: rgba(255, 255, 255, 0.7);
  font-size: 16px;
  line-height: 22px;
}

.footer__contacts-list-item {
  margin-bottom: 10px;
}
.footer__contacts-list-item:last-child {
  margin-bottom: 0;
}

.footer__contacts-list-item-phone {
  color: rgba(255, 255, 255, 0.7);
  font-size: 16px;
  line-height: 22px;
}

.form {
  position: relative;
  background-color: #00b3ff;
  padding: 50px 25px 90px 25px;
}
.form::after {
  display: none;
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  border: 420px solid transparent;
  border-left: 420px solid rgba(4, 24, 39, 0.5);
  border-top: 420px solid rgba(4, 24, 39, 0.5);
}
@media (min-width: 1920px) {
  .form::after {
    display: block;
  }
}
@media (min-width: 768px) {
  .form {
    padding: 50px 50px 125px 50px;
  }
}
@media (min-width: 1920px) {
  .form {
    display: flex;
    justify-content: flex-end;
    min-height: 840px;
    padding: 0;
  }
}

.form__decoration {
  display: none;
  position: absolute;
  z-index: 2;
  top: 245px;
  left: 350px;
}
@media (min-width: 1920px) {
  .form__decoration {
    display: block;
  }
}

.form__wrapper {
  position: relative;
}

.form__content-wrapper {
  padding: 0;
}
@media (min-width: 1920px) {
  .form__content-wrapper {
    padding: 170px 360px 0 0;
  }
}

.form__title {
  font-weight: 400;
  font-size: 30px;
  line-height: 42px;
  color: #ffffff;
  margin-top: 0;
  margin-bottom: 15px;
}
@media (min-width: 768px) {
  .form__title {
    font-size: 36px;
    line-height: 51px;
  }
}

.form__text {
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: #ffffff;
  margin-top: 0;
  margin-bottom: 30px;
}
@media (min-width: 768px) {
  .form__text {
    margin-bottom: 35px;
    font-size: 16px;
    line-height: 22px;
  }
}

.form__fields {
  border: none;
  margin: 0;
  padding: 0;
}

.form__fields-firstline {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
@media (min-width: 768px) {
  .form__fields-firstline {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px;
  }
}

.form__input {
  font-family: "PT Mono", "Arial", sans-serif;
  border: none;
  padding: 15px;
  font-size: 16px;
  line-height: 22px;
  background: rgba(4, 20, 39, 0.15);
  border-radius: 3px;
  color: #ffffff;
  outline: none;
  box-sizing: border-box;
}
.form__input--textarea {
  margin-top: 20px;
  width: 100%;
  margin-bottom: 35px;
}
.form__input::placeholder {
  color: #ffffff !important;
}

.form__ending-wrapper {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .form__ending-wrapper {
    flex-direction: row;
    gap: 50px;
    align-items: center;
  }
}

.form__button {
  display: block;
  cursor: pointer;
  padding: 15px 25px;
  width: 100%;
  box-sizing: border-box;
  background-color: #041827;
  color: #ffffff;
  border-radius: 3px;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}
.form__button:hover {
  box-shadow: 0 0 0 1px #ffffff;
}
@media (min-width: 768px) {
  .form__button {
    width: 265px;
  }
}

/*Checkbox*/
.form__checkbox {
  margin-top: 25px;
}
@media (min-width: 768px) {
  .form__checkbox {
    margin: 10px;
  }
}

.form__checkbox-label {
  position: relative;
  display: flex;
  align-items: center;
  gap: 10px;
}

.form__checkbox-text {
  font-family: "PT Mono", "Arial", sans-serif;
  display: block;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  margin: 0;
}

.form__checkbox-input[type=checkbox] {
  position: relative;
  width: 15px;
  height: 15px;
  outline: none;
  margin: 0;
  cursor: pointer;
}

.form__checkbox-mark {
  cursor: pointer;
  content: "";
  position: absolute;
  top: 50%;
  left: 7px;
  transform: translate(-50%, -50%);
  width: 15px;
